import emailjs from '@emailjs/browser';

const SERVICE_ID = 'service_n7ocitb';
const TEMPLATE_ID = 'template_brkuvix';
const PUBLIC_KEY = 'gdXdH04XLE7iZb3RT';

emailjs.init(PUBLIC_KEY);

export const sendEmail = async (templateParams) => {
  try {
    console.log(' Enviando correo con parmetros:', templateParams);
    
    const response = await emailjs.send(SERVICE_ID, TEMPLATE_ID, templateParams, PUBLIC_KEY);
    console.log(' Correo enviado correctamente:', response.status, response.text);
    return { success: true, response };
  } catch (error) {
    console.error(' Error al enviar correo:', error);
    return { success: false, error };
  }
};

export const generateHtmlTable = (data) => {
  if (!data || data.length === 0) {
    return '<p>No hay datos para mostrar en la tabla.</p>';
  }

  const headers = [
    'Departamento',
    'Grupo de asistencia',
    'Nombre',
    'Fecha',
    'Hora real del registro de entrada',
    'Hora real de registro de salida',
    'Grabacin de asistencia',
    'Duracin de la pausa',
    'Registros de descansos',
    'Periodo de tiempo'
  ];

  let tableHtml = `
    <div style="margin: 20px 0; overflow-x: auto;">
      <table style="width: 100%; border-collapse: collapse; font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; font-size: 13px; min-width: 800px; box-shadow: 0 2px 8px rgba(0,0,0,0.1);">
        <thead>
          <tr style="background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);">
  `;

  headers.forEach(header => {
    tableHtml += `<th style="border: 1px solid #e1e5e9; text-align: left; padding: 12px 10px; font-weight: 600; color: white; text-transform: uppercase; font-size: 12px; letter-spacing: 0.5px;">${header}</th>`;
  });

  tableHtml += `
          </tr>
        </thead>
        <tbody>
  `;

  data.forEach((row, index) => {
    const bgColor = index % 2 === 0 ? '#ffffff' : '#f8f9fa';
    const borderColor = index % 2 === 0 ? '#e9ecef' : '#dee2e6';
    
    tableHtml += `<tr style="background-color: ${bgColor}; border-bottom: 1px solid ${borderColor};">`;
    headers.forEach(header => {
      const value = row[header] || '-';
      const isDate = header === 'Fecha';
      const isTime = header.includes('Hora') || header.includes('Duracin');
      
      // Aplicar estilos especiales segn el tipo de dato
      let cellStyle = 'border: 1px solid #e1e5e9; text-align: left; padding: 10px 8px; color: #495057;';
      
      if (isDate) {
        cellStyle += 'font-weight: 600; color: #2c3e50;';
      } else if (isTime) {
        cellStyle += 'font-family: "Courier New", monospace; color: #6c757d;';
      }
      
      tableHtml += `<td style="${cellStyle}">${value}</td>`;
    });
    tableHtml += '</tr>';
  });

  tableHtml += `
        </tbody>
      </table>
    </div>
  `;

  return tableHtml;
};

// Funcin para generar el mensaje completo del correo
export const generateEmailMessage = (rows, title, options = {}) => {
  const { hasInfractions = false, blockLabel = 'General' } = options;
  
  // Funcin para generar la tabla HTML con valores en infraccin marcados en rojo
  const generateHtmlTable = (data) => {
    if (!data || data.length === 0) {
      return '<p>No hay datos para mostrar en la tabla.</p>';
    }

    const headers = [
      'Departamento',
      'Grupo de asistencia',
      'Nombre',
      'Fecha',
      'Hora real del registro de entrada',
      'Hora real de registro de salida',
      'Grabacin de asistencia',
      'Duracin de la pausa',
      'Registros de descansos',
      'Periodo de tiempo'
    ];

    let tableHtml = `
      <div style="margin: 20px 0; font-family: Arial, sans-serif;">
        <table style="width: 100%; max-width: 100%; border-collapse: collapse; border: 1px solid #ddd; font-size: 12px; line-height: 1.4;">
          <thead>
            <tr style="background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);">
    `;

    headers.forEach(header => {
      tableHtml += `<th style="border: 1px solid #ddd; padding: 8px; text-align: left; color: white; font-weight: bold; font-size: 11px; text-transform: uppercase; letter-spacing: 0.5px;">${header}</th>`;
    });

    tableHtml += `
            </tr>
          </thead>
          <tbody>
    `;

    data.forEach((row, index) => {
      const bgColor = index % 2 === 0 ? '#ffffff' : '#f8f9fa';
      
      tableHtml += `<tr style="background-color: ${bgColor};">`;
      headers.forEach(header => {
        const value = row[header] || '-';
        const isDate = header === 'Fecha';
        const isTime = header.includes('Hora') || header.includes('Duracin');
        
        // Verificar si este valor tiene infracciones
        let displayValue = value;
        if (row.classification && row.classification.violations.length > 0) {
          // Marcar en rojo si hay infracciones relacionadas con este campo
          const hasViolation = (
            (header === 'Hora real del registro de entrada' && row.classification.violations.some(v => v.includes('entrada'))) ||
            (header === 'Hora real de registro de salida' && row.classification.violations.some(v => v.includes('salida'))) ||
            (header === 'Duracin de la pausa' && row.classification.violations.some(v => v.includes('pausa')))
          );
          
          if (hasViolation) {
            displayValue = `<span style="color:#d32f2f;font-weight:600;background-color:#ffebee;padding:2px 4px;border-radius:3px;">${value}</span>`;
          }
        }
        
        // Aplicar estilos especiales segn el tipo de dato
        let cellStyle = 'border: 1px solid #ddd; padding: 6px; text-align: left; color: #333; word-wrap: break-word; max-width: 120px;';
        
        if (isDate) {
          cellStyle += 'font-weight: 600; color: #2c3e50;';
        } else if (isTime) {
          cellStyle += 'font-family: "Courier New", monospace; color: #6c757d;';
        }
        
        tableHtml += `<td style="${cellStyle}">${displayValue}</td>`;
      });
      tableHtml += '</tr>';
    });

    tableHtml += `
          </tbody>
        </table>
      </div>
    `;

    return tableHtml;
  };

  const htmlTable = generateHtmlTable(rows);
  
  // Bloque RRHH solo si hay infracciones
  const rrhhBlock = hasInfractions ? `
    <div style="background: #fff3cd; border: 1px solid #ffeaa7; border-radius: 8px; padding: 16px; margin: 20px 0; font-family: Arial, sans-serif;">
      <h3 style="color: #856404; margin: 0 0 12px 0; font-size: 16px; font-weight: 600;">
         Aviso de RRHH
      </h3>
      <p style="color: #856404; margin: 0 0 8px 0; font-size: 14px; line-height: 1.5;">
        Se detectaron registros fuera de horario o con pausas no permitidas.
      </p>
      <p style="color: #856404; margin: 0 0 8px 0; font-size: 14px; line-height: 1.5;">
        Para los colaboradores involucrados, agradeceremos indicar el motivo de la llegada tarde, la salida temprana o la variacin en la pausa.
      </p>
      <p style="color: #856404; margin: 0; font-size: 14px; line-height: 1.5;">
        Si existi una situacin excepcional (permiso, comisin, emergencia), por favor responder a este correo adjuntando la justificacin correspondiente.
      </p>
    </div>
  ` : '';
  
  return `
    <!DOCTYPE html>
    <html>
    <head>
      <meta charset="utf-8">
      <meta name="viewport" content="width=device-width, initial-scale=1.0">
      <title>Reporte de Asistencia - ${title}</title>
      <style>
        @media only screen and (max-width: 600px) {
          .container { width: 100% !important; padding: 10px !important; }
          .table-container { overflow-x: auto; }
          .table-container table { min-width: 600px; }
          .header { padding: 15px !important; }
          .content { padding: 15px !important; }
        }
      </style>
    </head>
    <body style="margin: 0; padding: 0; background-color: #f4f4f4; font-family: Arial, sans-serif;">
      <div class="container" style="max-width: 600px; margin: 0 auto; background-color: #ffffff;">
        
        <!-- Header con gradiente -->
        <div class="header" style="background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); padding: 20px; text-align: center; border-radius: 0;">
          <h1 style="color: white; margin: 0; font-size: 24px; font-weight: 700; text-shadow: 0 1px 2px rgba(0,0,0,0.3);">
             Reporte de Asistencia - ${title}
          </h1>
          <p style="color: white; margin: 8px 0 0 0; font-size: 14px; opacity: 0.9;">
            Bloque: ${blockLabel}
          </p>
        </div>
        
        <!-- Contenido del mensaje -->
        <div class="content" style="padding: 20px; background-color: #ffffff;">
          <p style="margin-bottom: 15px; color: #2d3748; font-size: 14px; line-height: 1.6;">
            <strong>Hola equipo,</strong>
          </p>
          
          <p style="margin-bottom: 20px; color: #4a5568; font-size: 14px; line-height: 1.6;">
            Adjunto encontrarn el reporte de asistencia correspondiente a la sucursal.
          </p>
        </div>
        
        <!-- Tabla de datos -->
        <div class="table-container" style="padding: 0 20px;">
          ${htmlTable}
        </div>
        
        <!-- Bloque RRHH (solo si hay infracciones) -->
        ${rrhhBlock}
        
        <!-- Footer -->
        <div style="padding: 20px; background-color: #ffffff; text-align: center; border-top: 1px solid #e2e8f0;">
          <p style="margin-bottom: 10px; color: #4a5568; font-size: 14px;">Saludos cordiales,</p>
          <p style="font-weight: 700; color: #2d3748; font-size: 16px; margin: 0;">${title}</p>
        </div>
        
      </div>
    </body>
    </html>
  `;
};

// Funcin para generar solo la tabla HTML (para casos donde solo se necesite la tabla)
export const generateHtmlTableOnly = (data) => {
  return generateHtmlTable(data);
};
